import React from "react";
import "./App.css";

import { books } from "./books";
import Book from "./book";
const BookList = () => {
  return (
    <section className="booklist">
      {books.map((book) => {
        return (
          <Book Key={book.id} {...book}>
            {" "}
          </Book>
        );
      })}
    </section>
  );
};

export default BookList;
